.tag {
    background: #ff6060;
    color: #fff;
    border-radius: 10px;
    width: 115px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 14px;
    padding: 5px;
    margin-right: 10px;
  }
  
  @media screen and (max-width: 740px) {
    .tag {
      font-size: 2.5vw;
      width: 20vw;
    }
  }
  