.gallery {
    text-align: center;
    margin: 60px 40px;
    padding: 70px 75px;
    background: #F6F6F6;
    border-radius: 25px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 50px;
  }
  
  @media screen and (max-width: 1000px) {
    .gallery {
      grid-template-columns: 1fr 1fr;
      grid-gap: 30px;
      margin: 60px 20px;
      padding: 50px;
    }
  }
  
  @media screen and (max-width: 740px) {
    .gallery {
      grid-template-columns: 1fr;
      margin: 20px;
      padding: 0;
      background: #fff;
      grid-gap: 20px;
    }
  }
  